*:hover {
  outline: none !important; }
*:active {
  outline: none !important; }
*:focus {
  outline: none !important; }
*:visited {
  outline: none !important; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1;
  overflow-x: hidden;
margin:0; }
blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }
.relative {
  position: relative !important; }

