    position: relative;
    font-size: 13px;
    font-weight: 600;
    color: #444;
  }
  .test-quesno-choice-counter::after{
    content:')';
    position: absolute;
    right: -4px;
    top: 0px;
  }
  .test-ques-choice-options {
    padding-left: 10px;
    font-size: 13px;
    font-weight: 600;
    color: #444;
}
.test-ques-correct-answer-section {
  position: relative;
  background-color: #94f2a4;
  border-radius: 5px;
}
.test-ques-correct-answer {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #01b846;
  color: #fff;
  left: 10px;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  line-height: 20px;
  vertical-align: middle;
}
.test-ques-correct-answer::after {
  position: absolute;
  content: '';
  width: 8px;
  height: 3px;
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  top: 25%;
  transform: translateY(-50%) rotate(-47deg) translateX(-50%);
  left: 42%;
}
